@import url("https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@500;600&display=swap");
html {
  font-size: rem(13);
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Barlow Semi Condensed", sans-serif;
  background-color: clr(neutral, light-gray);
}

main {
  height: 100%;
  display: grid;
  margin: 1.5rem;
}
main .content__container {
  /* thing to center */
  margin: auto;
}

.attribution {
  font-size: 1rem;
  text-align: center;
}
.attribution a {
  color: #3e52a3;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.username {
  font-family: "Barlow Semi Condensed", sans-serif;
  font-weight: 500;
}

.card__heading {
  font-family: "Barlow Semi Condensed", sans-serif;
  font-weight: 600;
}

.content__container {
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-areas: "one" "two" "three" "four" "five";
  gap: 1.5rem;
  padding: 0;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 40em) {
  .content__container {
    grid-template-areas: "one one" "two four" "three four" "five five";
    padding: 0;
  }
}
@media (min-width: 71.875em) {
  .content__container {
    grid-template-areas: "one one" "two three" "four four" "five five";
    padding: 0;
  }
}
@media (min-width: 87.5em) {
  .content__container {
    grid-template-areas: "one one two five" "three four four five";
    padding: 0;
  }
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.content__container > div {
  display: flex;
  flex-flow: column nowrap;
  padding: 2rem;
  border-radius: 0.625rem;
  box-shadow: 0 8px 30px rgba(193, 200, 209, 0.477);
}

.card__wrapper {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin-bottom: 1rem;
}
.card__wrapper .card__user-avatar {
  width: 1.75rem;
  height: 1.75rem;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.5);
}
.card__wrapper .card__user-info {
  margin-left: 1.25rem;
}
.card__wrapper .card__user-info .username {
  margin: 0;
  font-size: 0.75rem;
}
.card__wrapper .card__user-info .status {
  color: rgba(255, 255, 255, 0.5);
  margin: 0;
  font-size: 0.75rem;
}
.card__wrapper .card__user-info .status-d {
  color: rgba(72, 85, 106, 0.5);
  margin: 0;
  font-size: 0.75rem;
}

.card__heading {
  font-size: 1.27rem;
  margin: 0;
}

.card__review {
  font-size: 0.8125rem;
  line-height: 1.5;
  color: rgba(255, 255, 255, 0.7);
  margin: 1.5rem 0 0;
}

.card__review-d {
  font-size: 0.8125rem;
  line-height: 1.5;
  color: rgba(72, 85, 106, 0.7);
  margin: 1.5rem 0 0;
}

.container__card {
  background-image: url(/app/assets/images/bg-pattern-quotation.svg);
  background-position-x: 90%;
  background-repeat: no-repeat;
  background-color: #7541c8;
  color: white;
  grid-area: one;
}

.container__card-2 {
  color: white;
  background-color: #48556a;
  grid-area: two;
}

.container__card-3 {
  color: #48556a;
  background-color: white;
  grid-area: three;
}

.container__card-4 {
  color: white;
  background-color: #19212e;
  grid-area: four;
}

.container__card-5 {
  color: #48556a;
  background-color: white;
  grid-area: five;
}

@media (min-width: 40em) {
  .content__container .container__card {
    grid-column: 1/3;
  }
  .content__container .container__card-2 {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .content__container .container__card-3 {
    grid-column: 1/2;
    grid-row: 3/4;
  }
  .content__container .container__card-4 {
    grid-column: 2/3;
    grid-row: 2/4;
  }
  .content__container .container__card-5 {
    grid-column: 1/3;
    grid-row: 4/5;
  }
}
@media (min-width: 71.875em) {
  .content__container .container__card {
    grid-column: 1/3;
  }
  .content__container .container__card-2 {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .content__container .container__card-3 {
    grid-column: 2/3;
    grid-row: 2/3;
  }
  .content__container .container__card-4 {
    grid-column: 1/3;
    grid-row: 3/4;
  }
  .content__container .container__card-5 {
    grid-column: 1/3;
    grid-row: 4/5;
  }
}
@media (min-width: 87.5em) {
  .content__container .container__card {
    grid-column: span 2;
  }
  .content__container .container__card-2 {
    grid-column: 3/4;
    grid-row: 1/2;
  }
  .content__container .container__card-3 {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .content__container .container__card-4 {
    grid-column: 2/span 2;
    grid-row: 2/3;
  }
  .content__container .container__card-5 {
    grid-column: 4/5;
    grid-row: 1/3;
  }
}